import React from 'react';
import ReactGist from 'react-gist';
import './style.scss';

const Gist = ({ id, file }) => {
  if (!id) {
    return (null);
  }
  return (
    <ReactGist
      id={id}
      file={file}
      wrapperClass="gist-wrapper"
      titleClass="gist-title"
    />
  );
}

export default Gist;
